<template>
  <section class="video-section">
    <div class="video">
      <div class="video__container">
        <div class="responsive-wrapper responsive-wrapper--16_9">
          <iframe allowFullScreen frameBorder="0" className="video" :src="videoUrl"></iframe>
        </div>
      </div>
      <div class="video__info" v-if="selectedVideo">
        <h1 class="video__title">{{selectedVideo.snippet.title}}</h1>
        <p class="video__description">{{selectedVideo.snippet.description}}</p>
      </div>
    </div>

    <div class="swiper-container">
      <!-- Additional required wrapper -->
      <div class="swiper-wrapper">
        <!-- Slides -->
        <div :key="v.id.videoId" v-for="v in videos" class="swiper-slide" @click="selectedVideo = v">
          <img :src="v.snippet.thumbnails.medium.url" alt="">
        </div>
      </div>

      <!-- If we need navigation buttons -->
      <div class="swiper-button-prev"></div>
      <div class="swiper-button-next"></div>

      <!-- If we need scrollbar -->
      <div class="swiper-scrollbar"></div>
    </div>

    <!-- <ul class="video-list">
      <li class="video-item" :key="v.id.videoId" v-for="v in videos" @click="selectedVideo = v">
        <img class="video-item__thumb" :src="v.snippet.thumbnails.medium.url" alt="">
        <div class="video-item__info">
          <h4 class="video-item__title">{{v.snippet.title}}</h4>
        </div>
      </li>
    </ul> -->
  </section>
</template>

<script>
import {fetchVideos} from '@/api';

export default {
  data() {
    return {
      videoPayload: {},
      selectedVideo: null
    };
  },

  computed: {
    videos() {
      return this.videoPayload.items;
    },
    videoUrl() {
      if (!this.selectedVideo) {
        return '';
      }
      return `https://www.youtube.com/embed/${this.selectedVideo.id.videoId}`;
    }
  },

  async mounted() {
    this.videoPayload = await fetchVideos();
    await this.$nextTick();
    this._swiper = new Swiper('.swiper-container', {
      slidesPerView: 1,
      spaceBetween: 30,
      grabCursor: false,
      freeMode: true,
      mousewheel: true,

      // Navigation arrows
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev'
      },

      // And if we need scrollbar
      scrollbar: {
        el: '.swiper-scrollbar'
      },

      breakpoints: {
        720: {
          slidesPerView: 2,
          spaceBetween: 30
        },
        1000: {
          slidesPerView: 3,
          spaceBetween: 30
        },
        1280: {
          slidesPerView: 4,
          spaceBetween: 30
        }
      }
    });
    this.$el.querySelector('.swiper-slide:first-child').click();
  },

  beforeDestroy() {
    this._swiper.destroy();
  }
};
</script>

<style lang="scss" scoped>
.video-section {
}

.video {
  display: flex;
}

.video__container {
  flex-grow: 1;
}

.video__info {
  width: 40vw;
  margin-left: 2rem;
}

.video__title {
  margin-top: 0;
  margin-bottom: 2rem;
}

.video__description {
  border-top: 2px solid rgba(0,0,0,0.2);
  font-size: 1.3rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.video-list {
  margin-left: auto;
  overflow-y: auto;
  overflow-x: hidden;
  width: 420px;
  flex-shrink: 0;
  max-height: 70vh;
  padding-left: 2rem;
}

.video-item {
  cursor: pointer;
  transition: 0.2s;
  display: flex;
}

.video-item:hover {
  background-color: rgba(0,0,0,0.1);
}

.video-item + .video-item {
  margin-top: 1rem;
}

.video-item__thumb {
  max-width: 200px;
}

.video-item__info {
  padding-left: 1rem;
}

.video-item__title {
  margin-top: 0;
  // overflow: hidden;
  // text-overflow: ellipsis;
  // white-space: nowrap;
}

.swiper-container {
  // width: 100%;
  height: 250px;
  margin-top: 2rem;
}


@media screen and (max-width: 768px) {
  .video {
    display: block;
  }

  .video__info {
    width: auto;
    margin-left: 0;
    margin-top: 2rem;
  }

  .video-section {
    flex-direction: column;
  }

  .video-list  {
    border-top: 2px solid rgba(0,0,0,0.8);
    width: 100%;
    margin: 1rem 0 0 0;
    padding: 3rem 0 0 0;
  }
}
</style>
